html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  width: 100%;
  min-height: 100vh;
  font-family: "Raleway", sans-serif;
  position: relative;
}

a {
  display: block;
  text-decoration: none;
  color: black;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

@media (min-width: 1024px) {
  .contenedor {
    width: 80%;
  }
}
header {
  position: relative;
}

.navegacion {
  background-color: #1A120B;
  position: relative;
  z-index: 999;
  color: #E5E5CB;
  padding: 1rem 0;
}
.navegacion__contenedor {
  display: flex;
  justify-content: space-between;
}
.navegacion__links {
  position: absolute;
  background-color: #3C2A21;
  top: -100vh;
  left: 0;
  right: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  font-weight: 800;
  font-size: 2.2rem;
  transition: all 0.3s ease;
  opacity: 0;
}
@media (min-width: 768px) {
  .navegacion__links {
    position: relative;
    top: 0;
    height: auto;
    opacity: 1;
    background-color: #1A120B;
    flex-direction: row;
  }
}
.navegacion__link {
  color: #E5E5CB;
}
@media (min-width: 768px) {
  .navegacion__link {
    font-weight: 600;
    font-size: 1.8rem;
  }
  .navegacion__link::after {
    content: "";
    display: block;
    width: 0;
    height: 0.25rem;
    background-color: #E5E5CB;
    transition: all 0.3s ease;
  }
  .navegacion__link:hover::after {
    width: 70%;
  }
}
@media (min-width: 768px) {
  .navegacion__boton {
    display: none;
  }
}

.menu-activo {
  opacity: 1;
  top: 0;
}

.hero {
  width: 100%;
  position: relative;
}
.hero::after {
  position: absolute;
  z-index: -100;
  top: 0;
  bottom: 0;
  content: "";
  display: block;
  width: 100%;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 11%, rgba(0, 0, 0, 0.5) 100%), url(../img/hero-image.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.hero__container {
  padding: 10rem 0 15rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .hero__container {
    padding: 25rem 0;
  }
}
.hero__title {
  font-size: 4.5rem;
  text-align: center;
  color: #E5E5CB;
  -webkit-text-stroke: 0.15rem #1A120B;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 6rem;
  }
}
.hero__link {
  background-color: #1A120B;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  color: #E5E5CB;
  font-weight: 600;
}
@media (min-width: 768px) {
  .hero__link {
    border: 1px solid #E5E5CB;
  }
}
.hero .enlace {
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}
.hero .enlace::after, .hero .enlace::before {
  content: "";
  display: block;
  width: 6rem;
  height: 0.5rem;
  background-color: #E5E5CB;
}
.hero .enlace::after {
  margin-left: 1rem;
  animation-name: marcos;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.hero .enlace::before {
  margin-right: 1rem;
  animation-name: marcos2;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
@keyframes marcos {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(50deg) translateY(13rem);
    background-color: #1A120B;
    border: 1px solid white;
  }
  75% {
    transform: rotate(50deg) translateY(13rem);
    background-color: #1A120B;
    border: 1px solid white;
  }
  100% {
    transform: unset;
  }
}
@keyframes marcos2 {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-50deg) translateY(13rem);
    background-color: #1A120B;
    border: 1px solid white;
  }
  75% {
    transform: rotate(-50deg) translateY(13rem);
    background-color: #1A120B;
    border: 1px solid white;
  }
  100% {
    transform: unset;
  }
}

.hamburger {
  padding: 0;
}
.hamburger.is-active .hamburger-inner {
  background-color: #E5E5CB;
}
.hamburger.is-active .hamburger-inner::after, .hamburger.is-active .hamburger-inner::before {
  background-color: #E5E5CB;
}
.hamburger-inner {
  background-color: #E5E5CB;
}
.hamburger-inner::after, .hamburger-inner::before {
  background-color: #E5E5CB;
}

.nosotros {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .nosotros {
    padding: 10rem 0 0 0 0;
    flex-direction: row;
    gap: 6rem;
  }
}
.nosotros__descripcion {
  line-height: 1.6;
}
.nosotros__boton {
  width: 100%;
  text-align: center;
  background-color: #3C2A21;
  color: #E5E5CB;
  padding: 1.6rem 0;
  font-weight: 600;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .nosotros__boton {
    width: 45%;
  }
}
@media (min-width: 768px) {
  .nosotros__imagen {
    max-width: 40rem;
  }
}

.menu {
  margin-top: -1rem;
  padding: 10rem 0;
  background-color: #E5E5CB;
}
.menu__container {
  position: relative;
}
.menu__titulo {
  position: relative;
  margin-bottom: 6rem;
  font-size: 3.6rem;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__titulo {
    font-size: 4rem;
    text-align: left;
  }
  .menu__titulo::after {
    right: 0;
    content: "";
    position: absolute;
    width: 4rem;
    height: 4rem;
    background-image: url(../img/carrito.png);
    background-repeat: no-repeat;
    background-size: 4rem 4rem;
    animation: carro 2s alternate infinite;
  }
}

@keyframes carro {
  from {
    transform: translateX(0) rotate(360deg);
  }
  to {
    transform: translateX(-500%);
  }
}
.platos {
  position: relative;
}
@media (min-width: 768px) {
  .platos {
    display: grid;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.plato {
  padding: 2rem;
  background-color: #FFF;
  margin-bottom: 2rem;
  border-radius: 1.4rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
@media (min-width: 768px) {
  .plato {
    margin-bottom: 0;
  }
}
.plato:last-of-type {
  margin-bottom: 0;
}
.plato__borrar {
  font-weight: 800;
  color: #E5E5CB;
  background-color: #1A120B;
  padding: 0.5rem 0;
  width: 4rem;
  margin: 0 auto;
  text-align: center;
  border-radius: 0.5rem;
}
.plato__nombre {
  font-weight: 600;
  font-size: 2rem;
  color: #1A120B;
  margin: 1rem 0 0 0;
}
.plato__precio {
  font-weight: 600;
  font-size: 2rem;
}
.plato__descripcion {
  color: #3C2A21;
}
.plato__agregar {
  width: 100%;
  text-align: center;
  background-color: #1A120B;
  padding: 1rem 0;
  color: #E5E5CB;
  font-weight: 600;
}
.plato__imagen {
  display: flex;
  height: 18rem;
  object-fit: cover;
}
.plato__imagen img {
  flex: 1;
}
.plato--oferta {
  border: 0.25rem solid #1A120B;
  position: relative;
}
.plato--oferta::before {
  position: absolute;
  content: "POPULAR";
  color: #FFF;
  text-align: center;
  display: block;
  width: 40%;
  padding: 1rem 0;
  font-weight: 600;
  background-color: #1A120B;
  top: -1.8rem;
  transform: translateX(-4rem) rotate(-25deg) translateY(2.2rem);
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.carrito {
  left: 0;
  right: 0;
  z-index: 40;
  width: unset;
  height: unset;
  top: 0;
  background-color: #f3f3ec;
  padding: 2rem 3rem;
  color: black;
  max-width: 80rem;
}
.carrito__tabla {
  width: 100%;
}
.carrito tr {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
.carrito tr:first-child {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #D5CEA3;
}
.carrito tr.row {
  padding: 1rem 0;
}
.carrito tr td {
  text-align: center;
  flex: 1;
  padding: 1.6rem 0;
}
.carrito__opciones {
  display: flex;
  gap: 1rem;
}
.carrito__opciones a {
  flex: 1;
  text-align: center;
  font-weight: 600;
}
.carrito__opciones a:nth-child(1) {
  background-color: #E5E5CB;
  color: #1A120B;
  padding: 1rem 0;
}
.carrito__opciones a:nth-child(2) {
  background-color: #1A120B;
  color: #FFFFFF;
  padding: 1rem 0;
}
.carrito__nombre {
  width: 5rem;
}